<script>
import feather from 'feather-icons';
import Loader from '@/interface/template/Loader.vue';
import Subhead from '@/interface/template/outline/Subhead.vue';
import storageManager from '@/plugins/storage';

export default {
  inject: ['apiServer'],
  data() {
    return {
      hostkey: this.$Key,
      accessToken: storageManager.get('session', 'token'),
      configs: storageManager.get('configs'),
      listItems: [],
      loading_sources: true,
    }
  },
  components: {
    Loader,
    Subhead
  },
  methods: {
    async getData() {
      if (storageManager.get('session', 'login')) {
        try {
          this.loading_sources = false;

          const resAPI = await fetch("https://gateway.cloudrestfulapi.com/api/package/query", {
            method: 'POST',
            headers: { 'Content-Type': 'application/json', 'client-token-key': this.hostkey },
            body: JSON.stringify({
              method: 'find',
              args: [{}]
            })
          });

          const RestReturn = await resAPI.json();
          console.log(RestReturn);

          this.listItems = RestReturn;
          this.loading_sources = true;

        } catch (error) {
          console.log(error)
        }
      }
    },
    async deleteData(id) {
      if (storageManager.get('session', 'login')) {
        try {
          this.loading_sources = false;
          console.log("id", id)
          const resAPI = await fetch("https://gateway.cloudrestfulapi.com/api/package/" + id, {
            method: 'DELETE',
            headers: { 'Content-Type': 'application/json', 'client-token-key': this.hostkey },
          });
          console.log(await resAPI);
          this.loading_sources = true;
          await this.getData();

        } catch (error) {
          console.log(error)
        }
      }
    },
    async togglePackageStatus(packageId, currentStatus) {
      try {
        if (!confirm(`คุณต้องการ${currentStatus ? 'ปิดใช้งาน' : 'เปิดใช้งาน'} Package นี้หรือไม่?`)) {
          return;
        }

        this.loading_sources = false;

        const response = await fetch(`https://gateway.cloudrestfulapi.com/api/package/${packageId}`, {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
            'client-token-key': this.hostkey
          },
          body: JSON.stringify({
            data: {
              status: !currentStatus
            }
          })
        });

        if (response.ok) {
          await this.getData(); // Refresh the data
        } else {
          throw new Error('Failed to update package status');
        }

      } catch (error) {
        console.error('Error toggling package status:', error);
        this.loading_sources = true;
      }
    },
    getStatusClass(status) {
      return status 
        ? 'inline-flex px-2 py-1 text-xs font-semibold rounded-full bg-green-100 text-green-800'
        : 'inline-flex px-2 py-1 text-xs font-semibold rounded-full bg-red-100 text-red-800';
    },
    formatPrice(price) {
      return new Intl.NumberFormat('th-TH').format(price);
    },
    openPackageManagement() {
      this.$router.push('/origin/package/management');
    },
    updated() {
      feather.replace();
    },
  },
  async mounted() {
    try {
      await this.getData();
    } catch (error) {
      console.log(Error);
    }
  },
  setup() {
    //console.log("Setup");
  },
};
</script>

<template>
  <div v-if="!loading_sources" class="text-center">
    <Loader />
  </div>
  <Subhead v-if="loading_sources" :navigation="[
    {
      name: 'เพิ่ม Package',
      link: '/origin/package/add',
      style: 'plus',
      class: 'primary-btn',
      visible: true,
      type: 'button',
    },
  ]" />

  <div class="flex-1 pb-8 bg-gray-100 pt-2 pb-5 border-t" v-if="loading_sources">
    <div class="mt-8">
      <div class="mx-auto max-w-7xl px-6 sm:px-6 lg:px-6">

        <div class="mt-8 flex flex-col">
          <div class="-my-2 -mx-4 overflow-x-auto sm:-mx-6 lg:-mx-8">
            <div class="inline-block min-w-full py-2 align-middle md:px-6 lg:px-8">

              <div class="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">

                <div v-for="(package_item) in listItems" :key="package_item._id"
                  class="bg-white rounded-lg shadow-md overflow-hidden">
                  <div class="relative">
                    <div class="absolute top-2 right-2 bg-indigo-600 text-white px-3 py-1 text-sm font-semibold rounded-md">
                      {{ package_item.price }} บาท
                    </div>
                    <div class="h-48 bg-gradient-to-br from-indigo-500 to-purple-600 flex items-center justify-center">
                      <div class="text-center text-white">
                        <font-awesome-icon :icon="['fas', 'gift']" class="text-6xl mb-4" />
                        <h3 class="text-xl font-bold">{{ package_item.name }}</h3>
                      </div>
                    </div>
                  </div>
                  <div class="p-4">
                    <div class="mb-3">
                      <div class="flex items-center justify-between mb-2">
                        <p class="text-sm text-gray-600">{{ package_item.description }}</p>
                        <span :class="[
                          'inline-flex px-2 py-1 text-xs font-semibold rounded-full',
                          package_item.status ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'
                        ]">
                          {{ package_item.status ? 'เปิดใช้งาน' : 'ปิดใช้งาน' }}
                        </span>
                      </div>
                      <div class="grid grid-cols-2 gap-2 text-sm">
                        <div class="flex items-center">
                          <font-awesome-icon :icon="['fas', 'database']" class="text-indigo-600 mr-2" />
                          <span>Assets: {{ package_item.limits.maxAssets }}</span>
                        </div>
                        <div class="flex items-center">
                          <font-awesome-icon :icon="['fas', 'hdd']" class="text-indigo-600 mr-2" />
                          <span>{{ package_item.limits.storageGB }} GB</span>
                        </div>
                        <div class="flex items-center">
                          <font-awesome-icon :icon="['fas', 'users']" class="text-indigo-600 mr-2" />
                          <span>Users: {{ package_item.limits.maxUsers }}</span>
                        </div>
                        <div class="flex items-center">
                          <font-awesome-icon :icon="['fas', 'clock']" class="text-indigo-600 mr-2" />
                          <span>{{ package_item.duration }} วัน</span>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="bg-gray-100 border-t-2 border-gray-200 py-2 px-4 flex justify-between items-center">
                    <a href="javascript:void(0);" @click="togglePackageStatus(package_item._id, package_item.status)"
                      :class="[
                        'text-md font-medium',
                        package_item.status ? 'text-orange-600 hover:text-orange-800' : 'text-green-600 hover:text-green-800'
                      ]">
                      <font-awesome-icon 
                        :icon="['fas', package_item.status ? 'pause' : 'play']" 
                        class="mt-[3px] mr-2 text-md" />
                      <span>{{ package_item.status ? 'ปิดใช้งาน' : 'เปิดใช้งาน' }}</span>
                    </a>

                    <a href="javascript:void(0);" @click="$router.push('/origin/package/edit/' + package_item._id)"
                      class="text-gray-900 hover:text-gray-500 text-md font-medium">
                      <font-awesome-icon :icon="['fas', 'square-pen']" class="text-black mt-[3px] mr-2 text-md" />
                      <span>แก้ไข</span>
                    </a>

                    <a href="javascript:void(0);" @click="$router.push('/origin/package/detail/' + package_item._id)"
                      class="text-gray-900 hover:text-gray-500 text-md font-medium">
                      <font-awesome-icon :icon="['fas', 'eye']" class="text-black mt-[3px] mr-2 text-md" />
                      <span>ดู</span>
                    </a>

                    <a href="javascript:void(0);" @click="deleteData(package_item._id)"
                      class="text-red-600 hover:text-red-800 text-md font-medium">
                      <font-awesome-icon :icon="['fas', 'trash']" class="mt-[3px] mr-2 text-md" />
                      <span>ลบ</span>
                    </a>
                  </div>
                </div>

              </div>
            </div>
          </div>
        </div>

      </div>
    </div>
  </div>
</template>
